.Watchers {
    height: 100vh;
    overflow-y: scroll;
}

.Watchers-welcome {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    height: 100%;
    cursor: default;
}

.Watchers-welcome>h2 {
    padding: 0 30px;
    font-size: 3rem;
    color: lightblue;
}


.Watchers-welcome>p {
    padding: 0 10px;
    font-size: 1.2rem;
    color: #b9c1c3;
}

.Watchers-welcome-left {
    position: absolute;
    left: 25px;
    font-size: 3rem;
}



