.MultiWatch {
}

.MultiWatch-Toolbar {
    width: 100%;
    position: fixed;
    top: 0;
    border-bottom: 1px solid grey;
    left: 0;
}
.MultiWatch-Toolbar-opener {
    width: 100%;
    height: 20px;
    line-height: 20px;
    cursor: pointer;
    background-color: #18252e;
}

.MultiWatch-Markets {
    height: calc(100% - 20px);
    position: fixed;
    top: 20px;
    left: 0;
    display: -ms-flexbox;
    display: flex;
    border-right: 1px solid grey;
}

.MultiWatch-Markets-opener {
    height: 100%;
    width: 20px;
    line-height: 20px;
    cursor: pointer;
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
    text-orientation: sideways;
    background-color: #18252e;
    -webkit-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
            transform: rotate(180deg);
}

.MultiWatch-Watchers {
    position: relative;
    top: 20px;
    left: 20px;
    width: calc(100% - 20px);
}


